.plus {
  position: fixed;
  bottom: 1rem;
  left: 22%;
  transform: translateX(-50%);
  z-index: 9999;
  width: 50px;
  height: 50px;
  background-color: #16008b;
  border-radius: 50%;
  color: #fff;
  font-size: 40px;
}

@media (max-width: 768px) {
  .plus {
    left: 90%;
  }
}
